@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
a {
  text-decoration: none;
  color: #000;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  box-sizing: border-box;
  padding: 16px 0;
  height: 52px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 20px;
  border: 2px solid #444444;
  background-color: #fff;
}
.button--mobile {
  width: 260px;
}
.button:hover {
  background-color: #444444;
  color: #fff;
}
.button:focus .button__text {
  opacity: 0.3;
}
.main-nav {
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
}
.main-nav__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.main-nav__item {
  height: 79px;
}
.main-nav__link {
  position: relative;
  display: block;
  padding: 29px 0 27px 76px;
  border-top: 1px solid #ececec;
}
.main-nav__link:hover .main-nav__text {
  opacity: 0.6;
}
.main-nav__link:hover .main-nav__cart,
.main-nav__link:hover .main-nav__loop {
  color: #63d1bb;
  fill: #63d1bb;
}
.main-nav__link:focus .main-nav__text,
.main-nav__link:focus .main-nav__cart,
.main-nav__link:focus .main-nav__loop {
  opacity: 0.3;
}
.main-nav__list--second-part .main-nav__item:last-child .main-nav__link {
  border-bottom: 1px solid #ececec;
}
.main-nav__cart,
.main-nav__loop {
  position: absolute;
  top: 32px;
  left: 32px;
  color: #cccccc;
  fill: #cccccc;
}
.page {
  height: 100%;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  margin: 0;
  padding: 0;
  min-height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #fff;
}
.page-header__top {
  position: relative;
  height: 79px;
}
.page-header__logo {
  display: block;
  padding-left: 116px;
  padding-top: 22px;
  padding-bottom: 22px;
}
.page-header__button {
  position: absolute;
  width: 21px;
  height: 20px;
  top: 29px;
  right: 30px;
  border: none;
  outline: none;
  background-image: url("../../img/page-header-menu-close-icon.svg");
  background-repeat: no-repeat;
  background-color: transparent;
}
.promo {
  height: 489px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
  background-image: url("../../img/promo-background-image.jpg");
  background-repeat: no-repeat;
  background-position: 0 -20%;
}
@media (min-resolution: 2dppx) {
  .promo {
    background-image: url("../../img/promo-background-image@2x.jpg");
    background-size: 320px auto;
  }
}
.promo__title {
  margin: 0;
  padding: 0;
  padding: 0 30px;
  padding-top: 73px;
  text-align: center;
  font-size: 27px;
  line-height: 30px;
}
.promo__button {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 24px 36px 18px 30px;
  font-size: 19px;
  line-height: 24px;
  color: #fff;
}
.promo__button:first-of-type {
  margin-top: 120px;
  background-color: #63d1bb;
}
.promo__button:last-of-type {
  background-color: #56c3b2;
  padding-right: 27px;
}
.promo__button svg {
  opacity: 0.5;
}
.promo__button:hover:first-of-type {
  margin-top: 115px;
  padding-bottom: 23px;
}
.promo__button:hover:last-of-type {
  margin-top: -5px;
  padding-bottom: 23px;
}
.promo__button:hover svg {
  opacity: 0.7;
}
.promo__button:focus svg {
  opacity: 0.3;
}
.promo-text {
  width: 130px;
}
.product {
  position: relative;
  font-family: "Open Sans", "Arial", sans-serif;
}
.product::before {
  position: absolute;
  content: "";
  bottom: 0;
  background-image: url("../../img/zigzag-line.svg");
  width: 100%;
  height: 6px;
}
.product__wrapper {
  padding: 0 30px;
}
.product__title {
  margin: 0;
  padding: 0;
  margin-top: 31px;
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;
}
.product__subtitle {
  margin: 0;
  padding: 0;
  margin-top: 4px;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  text-transform: uppercase;
  color: #63d1bb;
}
.product__text {
  margin: 0;
  padding: 0;
  margin-top: 18px;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
}
.product__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  height: 39px;
  font-size: 15px;
  line-height: 20px;
  border-bottom: 1px solid #ececec;
  color: #444444;
}
.product__list:first-of-type {
  margin-top: 18px;
}
.product__list .product__definition {
  margin-left: 30px;
  width: 162px;
  text-align: left;
}
.product__image {
  margin-top: 28px;
}
.product__price {
  margin: 0;
  padding: 0;
  margin-top: 25px;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  text-align: center;
}
.product__button {
  margin-top: 22px;
  margin-bottom: 36px;
}
